* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 15px;
  color: hsla(0deg, 0%, 100%, 0.75);
  font-weight: 400;
  font-family: "Lexend Deca", sans-serif;
}

body {
  background-color: hsl(0deg, 0%, 95%);
  display: flex;
  justify-content: center;
}

.cards-wrapper {
  background-color: black;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  overflow: hidden;
}
.cards-wrapper .card {
  padding: 3rem;
}
.cards-wrapper .card svg {
  margin-bottom: 2.2rem;
}
.cards-wrapper .card .card-title {
  color: hsl(0deg, 0%, 95%);
  font-size: 2.7rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 2rem;
  font-family: "Big Shoulders Display", cursive;
}
.cards-wrapper .card .card-description {
  font-size: 1rem;
  margin-bottom: 1.7rem;
  line-height: 1.7rem;
}
.cards-wrapper .card .btn {
  display: block;
  width: fit-content;
  background-color: hsl(0deg, 0%, 95%);
  padding: 0.9rem 2.2rem;
  font-size: 1rem;
  font-weight: 700;
  text-decoration: none;
  border-radius: 10rem;
  border: 1px solid hsl(0deg, 0%, 95%);
  transition: background-color 0.15s ease-in-out, color 0.15s ease-in-out;
}
.cards-wrapper .card .btn:hover {
  background-color: transparent;
  cursor: pointer;
}
.cards-wrapper #sedans {
  background-color: hsl(31deg, 77%, 52%);
}
.cards-wrapper #sedans .btn {
  color: rgba(227, 136, 38, 0.8);
}
.cards-wrapper #suvs {
  background-color: hsl(184deg, 100%, 22%);
}
.cards-wrapper #suvs .btn {
  color: rgba(0, 105, 112, 0.8);
}
.cards-wrapper #luxury {
  background-color: hsl(179deg, 100%, 13%);
}
.cards-wrapper #luxury .btn {
  color: rgba(0, 66, 65, 0.8);
}
.cards-wrapper #sedans .btn:hover, .cards-wrapper #suvs .btn:hover, .cards-wrapper #luxury .btn:hover {
  color: hsl(0deg, 0%, 95%);
}

@media screen and (min-width: 320px) {
  .cards-wrapper {
    width: calc(100% - 50px);
    margin: 13vh 0;
    border-radius: 1rem;
  }
}
@media screen and (min-width: 765px) {
  body {
    align-items: center;
    height: 100vh;
  }
  .cards-wrapper {
    grid-template-columns: repeat(3, 1fr);
    max-width: 920px;
    margin: 0;
  }
  .cards-wrapper .card {
    padding: 3.2rem;
    position: relative;
  }
  .cards-wrapper .card .card-description {
    margin-bottom: 8rem;
  }
  .cards-wrapper .card .btn {
    position: absolute;
    bottom: 3.2rem;
  }
}